import React from 'react'
import Services from 'ezyvuecore/services/services';
import LoadingOverlay from 'common/loading/loadingOverlay'

export default class ContactPage extends React.Component {
    constructor() {
        super();
        this.state = {
            error: '',
            submitted: false,
            showLoadingScreen: false
        };

        this.onSubmit = this.onSubmit.bind(this);
    }

    displayError(error) {
        this.setState({ error: error, showLoadingScreen: false });
    }

    async onSubmit(e, name, email, message) {
        try {
            this.setState({ showLoadingScreen: true });

            const resp = await Services.adminService.sendMessage({
                firstName: name,
                email: email,
                message: message,
            });

            if (!resp.ok)
                throw Error();

            this.setState({ submitted: true, showLoadingScreen: false });
        }
        catch (e) {
            this.displayError('An error occured submitting the form. Please try again.');
        }
    }

    render() {
        if (this.state.submitted)
            return (
                <div className='center-page-xy center std-page'>
                    <div className='centered-page-box' style={{ textAlign: 'center' }}>
                        <div>
                            <img src={require('assets/images/emailsent.jpg')} alt='' style={{ width: '128px', marginBottom: '60px' }} />
                        </div>
                        <h3>Thank you for getting in touch!</h3>
                        We&apos;ll get back to you as soon as possible.
                    </div>
                </div>
            );
        return (
            <LoadingOverlay show={this.state.showLoadingScreen}>
                <div className='center-page-xy center std-page'>
                    <div className='centered-page-box' style={{ textAlign: 'center', fontSize: 'large' }}>
                        <h1>Get in touch</h1>
                        <div className='center-x'>
                            <img src={require('assets/images/emailsent.jpg')} style={{ width: '100px', margin: '30px 0' }} alt='' />
                            <div>
                                General enquires<br /> <a href="mailto:enquiries@ezyvue.com">enquiries@ezyvue.com</a><br /><br />
                                Technical support<br /> <a href="mailto:support@ezyvue.com">support@ezyvue.com</a><br /><br />
                                Sales<br /> <a href="mailto:sales@ezyvue.com">sales@ezyvue.com</a><br /><br />
                                Investor Enquiries<br /> <a href="mailto:investors@ezyvue.com">investors@ezyvue.com</a><br /><br />
                            </div>
                        </div>
                    </div>
                </div>
            </LoadingOverlay>
        );
    }
}