import React, {Fragment} from 'react';
import Backdrop from '@material-ui/core/Backdrop';
import CircularProgressWithLabel from './circularProgressWithLabel';
import { makeStyles } from '@material-ui/core/styles';
import PropTypes from 'prop-types';

const useStyles = makeStyles(() => ({
  backdrop: {
    zIndex: 5,
    color: 'white',
    backgroundColor: 'rgb(255, 255, 255)'
  },
}));

export default function LoadingOverlay(props) {
  const classes = useStyles();

  return (
      <Fragment>
        <Backdrop className={classes.backdrop} open={props.show} transitionDuration={{exit: 1000, enter: 0, appear: 0}}>
            <div style={{textAlign: "center"}}>
                <CircularProgressWithLabel variant={props.variant} color="primary" value={props.value} size={85} fontSize='24px' />
            </div>
        </Backdrop>
        {props.children}
      </Fragment>
  );
}

LoadingOverlay.defaultProps = {
  variant: 'indeterminate',
  value: 0
};

LoadingOverlay.propTypes = {
  show: PropTypes.bool.isRequired,
  value: PropTypes.number,
  children: PropTypes.oneOfType([
    PropTypes.array,
    PropTypes.object
  ]),
  variant: PropTypes.string // indeterminate or determinate
};